.container_projetos{
    position: fixed;
    width: 70%;
    height: 80%;
    left: 15%;
    top: 20%;
    display: flex;
    flex-wrap: wrap;
    opacity: 0;
    user-select: none;
    pointer-events: none;
    transition: all 0.4s ease-in-out;
    z-index: 1;
}
.container_projetos.open{
    opacity: 1;
    pointer-events: all;
    transition-delay: 0.4s;
    transform: translateX(0);
}
.card_projeto{
    height: auto;
    width: 40%;
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 2%;
    margin-bottom: 5%;
    min-height: 7%;
    border-radius: 6px;
    box-shadow: 18px 18px 13px rgb(15, 15, 15); 
    cursor: pointer;
    background-color: rgba(80, 80, 80, 0.205);
    position: relative;
    transition: all 0.5s ease-in-out;
    cursor: pointer;
    animation: filtranimation 0.6s linear forwards;
    z-index: 9999;
}
.card_projeto:hover{
    background-color: rgba(127, 127, 127, 0.205);
}

.card_projeto:hover .img_projeto img{
  transform: scale(1.1);
}
.img_projeto{
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  top: 5%;
  left: 3%;
  width: 94%;
  height: 45%;
}
.img_projeto img{
  max-width: 100%;
  max-height: 100%;
  user-select: none;
  transition: all 0.2s ease-in-out;
}
.sep_line{
  position: relative;
  display: flex;
  top: 8%;
  left: 2.5%;
  width: 95%;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.4);
}
.sep_line::before{
  content: '';
  position: absolute;
  background-color: rgba(255, 255, 255, 0.6);
  top: -1px;
  left: -2px;
  height: 4px;
  width: 4px;   
  transform: rotate(45deg);
}
.sep_line::after{
  content: '';
  position: absolute;
  background-color: rgba(255, 255, 255, 0.6);
  top: -1px;
  right: -2px;
  height: 4px;
  width: 4px;  
  transform: rotate(45deg);
}
.card_projeto h2{
  width: 90%;
  font-size: 17pt;
  text-align: center;
  margin-top: 50px;
  padding: 5%;
  color: rgba(215, 215, 215, 0.8);
}
.card_projeto h4{
  position: absolute;
  width: auto;
  text-align: center;
  left: 3%;
  font-size: 9pt;
  top: -26px;
  border-radius: 5px;
  padding: 2px 10px 2px 10px;
  background-color: rgb(190, 190, 190);
  color: rgb(20, 20, 20);
}
.card_projeto p{
  width: 90%;
  position: relative;
  top: -15px;
  height: 18%;
  text-align: center;
  overflow: auto;
  vertical-align: middle;
  font-size: 13pt;
  margin-top: 0;
  padding: 5%;
  color: rgba(215, 215, 215, 0.8);
}
.card_projeto:hover .saiba_mais{
  opacity: 1;
  transform: translateY(0);
}
.saiba_mais{
  width: 100%;
  opacity: 0;
  position: absolute;
  text-align: center;
  bottom: 0;
  padding: 5%;
  transform: translateY(50%);
  background-color: transparent;
  cursor: pointer;
  border: none;
  color: rgb(215, 215, 215);
  transition: all 0.3s ease-in-out;
}
.scroll::-webkit-scrollbar {
  width: 6px; 
}
.news {
  font-family: 'Arial', sans-serif;
  font-size: 16px;
  font-weight: bold;
  color: #ffffff !important;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  position: absolute;
  top: -20px;
  right: 20px;
  border: 2px solid rgb(55, 55, 55);
  padding: 10px 20px;
  background-color: #252525c0; /* Fundo amarelo chamativo */
  border-radius: 5px;
  overflow: hidden;
  z-index: 99;
}
/* Estilos para a borda animada */
.news::before {
  content: "";
  position: absolute;
  left: -2px;
  right: -2px;
  bottom: -2px;
  opacity: 0;
  height: 4px;
  background-color: #ff6347; /* Cor da borda */
  border-radius: 5px;
  animation: percorrerLeft 3s linear infinite; /* Animação da borda */
}
.news::after {
  content: "";
  position: absolute;
  opacity: 0;
  top: -2px;
  left: -2px;
  right: -2px;
  height: 4px;
  background: linear-gradient(to right, black, rgb(0, 119, 255));
  border-radius: 5px;
  animation: percorrerRight 3s linear infinite; /* Animação da borda */
  animation-delay: 1.5s;
}

@keyframes percorrerLeft {
  0% {
    opacity: 1;
    transform: translateX(100%);
    background: linear-gradient(to right, red, black);
  }
  25% {
    background: linear-gradient(to right, rgb(255, 0, 119), black);
  }
  50% {
    background: linear-gradient(to right, rgb(255, 0, 179), black);
  }
  65% {
    background: linear-gradient(to right, rgb(153, 0, 255), black);
  }
  90% {
    transform: translateX(-100%);
    background: linear-gradient(to right, rgb(0, 119, 255), black);
  }
}

@keyframes percorrerRight {
  0% {
    opacity: 1;
    transform: translateX(-100%);
    background: linear-gradient(to right, black, rgb(0, 119, 255));
  }
  25% {
    background: linear-gradient(to right, black, rgb(153, 0, 255));
  }
  50% {
    background: linear-gradient(to right, black, rgb(255, 0, 179));
  }
  65% {
    background: linear-gradient(to right, black, rgb(255, 0, 119));
  }
  90% {
    transform: translateX(100%);
    background: linear-gradient(to right, black, red);
  }
}

/* responsividade */
@media (min-width: 601px) and (max-width: 1601px) {
  h2{
    margin-top: 10% !important;
    font-size: 14pt !important;
  }
  .card_projeto{
    height: 85%;
  }
.card_projeto p{
  height: 15%;
  padding-top: 0;
  top: -20px;
  font-size: 11pt;
}
}

@media only screen and (max-width: 600px) {
  .container_projetos{
    width: 100%;
    top: 10%;
    height: 75%;
    padding: 5px !important;
    padding-top: 25px !important;
    left: 0;
  }
  .card_projeto{
    width: 95%;
    margin-bottom: 15%;
  }
  .card_projeto p{
    height: auto;
  }
  .saiba_mais{
    display: none;
  }
  .img_projeto{
    height: 170px;
  }
  .scroll::-webkit-scrollbar-thumb {
    background-color: transparent;
  }
  .scroll::-webkit-scrollbar-thumb:hover {
    background-color: transparent;
  }
}
@media only screen and (min-width: 601px) and (max-width: 1250px) {
  .container_projetos{
    width: 100%;
    top: 10%;
    height: 75%;
    padding: 5px !important;
    left: 0;
  }
  .card_projeto{
    height: auto;
  }
  .card_projeto p{
    height: 15%;
  }
}